import { Button, FloatingLabel, Form, FormGroup } from "react-bootstrap"

const Formulario = () => {
    return (
            <> 
                <h2> Crear Cita</h2> 
                <Form>
                    <FormGroup>
                        <FloatingLabel
                        controlId="floatingInput"
                        label="Nombre de mascota"
                        className="mb-3 p-2"
                        >
                            <Form.Control 
                                type="name" 
                                placeholder="Firulais" 
                            />   
                        </FloatingLabel>
                    <FloatingLabel 
                        controlId="dueño" 
                        label="Nombre Dueño"
                        className="p-2"
                    >
                        <Form.Control 
                            type="name" 
                            placeholder="Nombre dueño" 
                        />
                    </FloatingLabel>
                    <FloatingLabel 
                        controlId="fecha" 
                        label="Fecha"
                        className="p-2"
                    >
                        <Form.Control 
                            type="date" 
                            placeholder="Nombre dueño" 
                        />
                    </FloatingLabel>
                    <FloatingLabel 
                        controlId="hora" 
                        label="hora"
                        className="p-2"
                    >
                        <Form.Control 
                            type="time" 
                            placeholder="Nombre dueño" 
                        />
                    </FloatingLabel>
                    <FloatingLabel 
                        controlId="floatingTextarea" 
                        label="Comments" 
                        className="mb-3 p-2">
                        <Form.Control as="textarea" placeholder="Leave a comment here" />
                    </FloatingLabel>
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                    </FormGroup>
                    

                </Form>
            </>  
            );
}
 
export default Formulario;