import { Container , Row , Col} from 'react-bootstrap'
import Formulario from './Formulario';
function Citas (){


    return(
        <>
            <Container>
                <Row>
                    <Col>
                        <Formulario/>
                    </Col>
                    <Col>
                        <h2>Listado de citas</h2>
                    </Col>
                </Row>
            </Container>
        </>
    )
}
export default Citas;